import React from 'react'
import { Dropdown, DropdownButton, InputGroup, FormControl } from 'react-bootstrap'

export default function DropdownBtn(props) {
    const {addInput, input, title, dropSelect} = props
    return (
        <>
           <InputGroup className="mb-3" value='text'>
                <FormControl type="text" aria-label="Text input with dropdown button" onChange={(e) => addInput(e.target.value)} value={input} />
                <DropdownButton
                    variant="outline-secondary"
                    title={title}
                    id="input-group-dropdown-2"
                    align="end"
                >
                       
                {dropSelect!== undefined ? dropSelect.map((element,index) => {
                    return(
                        <Dropdown.Item key={index} value={element} onClick={(e) => addInput(e.target.outerText, true) }>{element}</Dropdown.Item>
                    )
                }) : <></>            
                }
               
                </DropdownButton>
            </InputGroup>
        </>
    )
}

