import {React, useState} from 'react'
import MapView from './MapView'
import ModalView from './ModalWrapper'

function MapWrapper(props) {
    
    const [mapKey, setMapKey]   = useState(0)
    const [detail, setDetail]   = useState('')
    const [show, setShow]       = useState(false)
    
    const {places} = props

    const mapStyle = {
        small : {
            width : '200px',
            height : '120px',
            radius : '10%'
        },
        big : {
            width : '500px',
            height : '300px',
            radius : '5%'
        }
    }

    
    const handleClose = () =>  setShow(false);
    const handleShow = (item) => {
        const tmpKey = Math.ceil(Math.random()*5)
        setMapKey(tmpKey)
        setDetail(item)
        setShow(true)};

    return (
        <>
            <div className='mapWrapper d-flex flex-row justify-content-around'>
                <div onClick={() => handleShow(places)}>
                             <MapView zoom={15} mapKey={9} style={mapStyle.small} length={places.length} element={places} zoomControl={false}  />
                </div>
                <ModalView mapKey={mapKey} show={show} size='lg' fullscreen={false} type='map' handleClose={handleClose} handleShow={handleShow} headline={detail.name} length={detail.length} element={detail} style={mapStyle.big}/>
             </div>
        </>
     )
}

export default MapWrapper
