import React from 'react'
import { Form } from 'react-bootstrap'

export default function FormGroup(props) {
    const {title, id, as, placeholder, value, changeFunc, rows} = props

    return (
        <>
          <Form.Group className="mb-3" controlId={id}>
                    <Form.Label>{title}</Form.Label>
                    <Form.Control as={as} rows={rows} placeholder={placeholder} value={value} onChange={(e) => changeFunc(e.target.value) }/>
                </Form.Group>  
        </>
    )
}
