import React, {useState, useEffect, useRef} from 'react'
import { Map, TileLayer, Marker, Popup } from 'react-leaflet'
import L from 'leaflet'

function MapView(props) {
 
   const {element} = props
  
    const mapRef = useRef()
    const [keyState, setKeyState] = useState(8);
 
       useEffect( () => {
        const {current ={} } = mapRef
        const {leafletElement: map} = current;
        setTimeout( () => map.invalidateSize(false), 100)

        }, [mapRef])
    // if (!element.length && !element.height){
    //     console.log('we are not existing')
    // }
  console.log(element.name, element.length, element.width, element, 'show me this')  
  return (
        <>
            <Map ref={mapRef} key={props.mapKey} center={[element.length, element.width]} 
                zoom={props.zoom} 
                scrollWheelZoom={false}
                attributionControl={false}
                zoomControl={props.zoomControl} 
                style={{width: props.style.width, height: props.style.height, borderRadius: props.style.radius}}>
                <TileLayer url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"/>
                <Marker position={[element.length, element.width]}>
                    <Popup> 
                          <h6>{element.street}</h6><p>open at {element.hours}</p>
                    </Popup>
                </Marker>
                </Map>
        </>)
}

export default MapView
