import React, {useState, useEffect} from 'react'
import {Editor, EditorState, RichUtils, convertToRaw, convertFromRaw} from 'draft-js';

export default function EditorWrapper(props) {
    
    const blockType = [
        {run: 'ordered-list-item', btn: 'ol'},
        {run: 'unordered-list-item', btn: 'ul'},
        {run: 'blockquote', btn: 'block'},
        {run : 'header-one', btn: 'h1'},
        {run: 'header-two', btn: 'h2'},
        {run: 'header-three', btn: 'h3'},
        {run: 'header-four', btn: 'h4'},
        {run: 'header-five', btn: 'h5'},
        {run: 'header-six', btn: 'h6'}
        ]
    const inlineStyle = [{run: 'ITALIC',btn: 'I'}, {run: 'BOLD', btn: 'B'}, {run: 'UNDERLINE', btn: 'U'}, {run: 'CODE', btn: '<code>'}]
   
    let {exportDraft, setExportDraft} = props

    const [editOn, setEditOn] = useState(false)
    const [editorState, setEditorState] = React.useState(
        () => EditorState.createEmpty(),);

    const currentInlineStyle    = editorState.getCurrentInlineStyle()
    const currentBlockType      = RichUtils.getCurrentBlockType(editorState)

//Functions
    function toggleInlineStyle(event, type) {
        event.preventDefault();
        let newState = RichUtils.toggleInlineStyle(editorState, type)
        onChange(newState)
        }

    function toggleBlockType(event, type){
        event.preventDefault();
        let newState = RichUtils.toggleBlockType(editorState, type)
        onChange(newState)
    }
    

    function onChange(editorState) {
        setEditorState(editorState);
        const raw = convertToRaw(editorState.getCurrentContent());
        setExportDraft(raw)
    }

    useEffect(() => {
        if (exportDraft) {
             const content = convertFromRaw(exportDraft)
             setEditorState(() => EditorState.createWithContent(content))}
    }, [])

    return (
        <div>
            <h6>Article<span style={{color: 'lightgray'}}> - without the teaser!</span></h6>
            <div className='editorBlock'>
                {blockType.map((element) => {
                    return(
                        <input
                            className = {currentBlockType === element.run ? 'editBtn activeEdit' : 'editBtn' }
                            type='button'
                            value={element.btn}
                            onMouseDown={(e) => toggleBlockType(e, element.run)}
                        /> 
                    )
                })}
                {inlineStyle.map((element) => {
                    return(
                        <input
                            className = {currentInlineStyle.size>0 && currentInlineStyle.has(element.run) ? 'editBtn activeEdit' : 'editBtn' }
                            type='button'
                            value={element.btn}
                            onMouseDown={(e) => toggleInlineStyle(e, element.run)}
                        /> 
                    )
                })}
                <div className="draft-editor-wrapper">
                    <Editor
                        editorState={editorState}
                        onChange={onChange} />
                </div>
            </div>
            <br></br>
        </div>
    )
}
