import React, {useState, useContext} from 'react'
import { Container, ListGroup, Button } from 'react-bootstrap'
import { ArticleContext, LangContext } from '../../context'

import ManageText from './ManageText'

export default function EditText(props) {
    const {collection, setCollection} = useContext(ArticleContext)
    const {lang, setLang} = useContext(LangContext)
    const [editMode, setEditMode] = useState(false)
    const [articleChange, setArticleChange] = useState('')

    function handleSubmit(article){
        setArticleChange(article)
        setEditMode(true)
             
   } 

    return (
        <Container>
            {!editMode ? 
            <>
            <div className='d-flex justify-content-between'>
                <p>{lang === 'en' ? 'Following english article are available for editing' : 'Folgende Artikel in deutscher Ãœbersetzung liegen vor'} </p>
                <Button variant='secondary' onClick={() => setLang(() => lang==='en' ? 'de' : 'en')}>{lang === 'en' ? 'show german article' : 'zeige englische Artikel'}</Button>
            </div>
            <ListGroup>
                 {collection.map( (element) => {
                      return (
                        <ListGroup.Item action variant="secondary" onClick={() => handleSubmit(element)}>
                           {element.headline }
                        </ListGroup.Item>
                     )
                      })}
            </ListGroup>
            </> :
            <ManageText edit={true} article={articleChange} close={props.close} />        



}
        </Container>
            
       
    )
}
