import React, {useState, useContext} from 'react'
import { Container, Row, Col, Button, Form } from 'react-bootstrap'
import AuthContext from '../../context'
import userArr from '../../data/authUser.json'


function Login() {
    const [check, setCheck] = useState(false) 
    const [user, setUser] = useState('');
    const [pass, setPass] = useState('');
    const {authLogin, setAuthLogin} = useContext(AuthContext)


    function handleSubmit(e){
        e.preventDefault();
         userArr.map((element) => {
               element.user===user && element.passwd ===pass ? setAuthLogin(element.user) : console.log('wrong input')
        })
        setUser('');
        setPass('');
        
    }


    function handleChange(e, type){
        if (user.length > 2 && pass.length>2) {
            setCheck(true)
        }
        else {
            setCheck(false)
        }
        type==='use' ? setUser(e.target.value) : setPass(e.target.value) 
    }

    return (
        
        <Container className='pageWrapper' style={{textAlign: 'left'}}>
            <Row className='justify-content-md-center'>
                <Col lg="8">
                    <div className='login'></div>
                    <h3>Login Page</h3>
                    <Form onSubmit={(e) => handleSubmit(e)}>
                        <Form.Group className="mb-3" controlId="formBasicEmail">
                            <Form.Label>Username</Form.Label>
                            <Form.Control value={user} type="text" placeholder="Enter username" onChange={(e) => handleChange(e, 'use')}/>
                            <Form.Text className="text-muted">
                           
                            </Form.Text>
                        </Form.Group>

                        <Form.Group className="mb-3" controlId="formBasicPassword">
                            <Form.Label>Password</Form.Label>
                            <Form.Control value={pass}type="password" placeholder="Password" onChange={(e) => handleChange(e, 'pass')}/>
                        </Form.Group>
                        {check ? <Button variant="primary" type="submit" active>Submit</Button> : <Button variant="secondary" disabled>Please fill the fields</Button>}
                    </Form>
                </Col>
            </Row>
        </Container>
    )
}

export default Login
