import React from 'react'
import About from './Pages/About'
import Counsel from './Pages/Counseling'
import { Container, Row, Col } from 'react-bootstrap'

export default function PageWrapper(props) {
    

    return (
        <Container className='pageWrapper'>
            <Row>
                {props.src ==='about' ?
                   <About /> :
                props.src ==='counsel' ?
                   <Counsel />  :
                <Col>
                    <h3>{props.title} </h3>
                    <p>This is a generated page without component...</p>
                </Col>
                }
            </Row>
            
        </Container>
    )
}
