import React, {useContext, useEffect} from 'react'
import {Navbar, Container, Nav, Row, Form} from 'react-bootstrap';
import { EmojiSmile, PeopleFill, ChatText } from 'react-bootstrap-icons'; 
import {Link} from 'react-router-dom'
import AuthContext, { LangContext, ModalContext } from '../../context'
import logo from '../../images/logo.png'
import ModalView from '../ModalWrapper'


export default function Navigation() {
   
    const {authLogin, setAuthLogin, fetchData, userNavbar, adminNavbar} = useContext(AuthContext)
    const {modalShow, setModalShow, modalType, setModalType, handleClose} = useContext(ModalContext)
    const {lang, setLang} =useContext(LangContext)
    
    //Functions
    const handleLogout = () => setAuthLogin(false)

    const handleShow = (element) => {
        setModalType([element.type, element.name])
        setModalShow(true)
    };

    useEffect(() => {
        fetchData('UserNavbar')
      },[])

      useEffect(() => {
        fetchData('UserNavbar')
      },[lang])  

    useEffect(() => {
        fetchData('AdminNavbar')
    },[])

    useEffect(() => {
        fetchData('AdminNavbar')
    },[lang])

      
    return (
        <>
        <Row>
        <Navbar bg="myBg" variant='dark' sticky='top' collapseOnSelect expand='lg'>
             <Container>
                  <Navbar.Brand>
                  <Link to='/' style={{textDecoration : 'none', color: 'white'}}><img src={logo} style={{width: '100px'}} />A group site</Link>
                  </Navbar.Brand>
                  
                  <Navbar.Toggle aria-controls="basic-navbar-nav" />
                  <Navbar.Collapse id="basic-navbar-nav" className="justify-content-end">
                        <Nav className="navbar" >
                            {userNavbar.map((element, index) => {
                                return(
                                    <Nav.Link eventKey={index}><Link to={element.link} style={{textDecoration: 'none', color: '#800000'}}>{element.title}</Link></Nav.Link>  
                                )
                            })}
                            {authLogin ?
                                <Nav.Link eventKey="100" style={{textDecoration : 'none', color: '#000000', marginLeft: '20px'}} onClick={handleLogout}><EmojiSmile /> {authLogin} (Logout)</Nav.Link> :
                                <Nav.Link eventKey="99"><Link to='/login' style={{textDecoration : 'none', color: '#800000', marginLeft: '20px'}}><PeopleFill />Login</Link></Nav.Link>
                            }
                        {lang === 'en' ? <div style={{cursor: 'pointer', marginLeft: '10px'}} onClick={() => setLang('de')}><ChatText /> deutsch</div> : <div style={{cursor: 'pointer', marginLeft: '10px'}} onClick={() => setLang('en')}><ChatText /> english</div>}
                       </Nav>
                </Navbar.Collapse>
            </Container>
        </Navbar>
        </Row>
        <Row>
            {authLogin ? (<Navbar bg="adminBg" variant='dark' sticky='top' collapseOnSelect expand='lg'>
                <Container>
                     <Navbar.Brand>
                     ðŸ”§ Admin dashboard ðŸ”§
                     </Navbar.Brand>
                     
                     <Navbar.Toggle aria-controls="basic-navbar-nav" />
                     <Navbar.Collapse id="basic-navbar-nav" className="justify-content-end">
                           <Nav className="navbar">
                             {adminNavbar.map( (element) => {
                                 return(
                                    <Nav.Link style={{textDecoration : 'none', color: 'lightgrey'}} onClick={() => handleShow(element)}>{element.icon} {element.name}</Nav.Link>
                                 )
                             })}
                              <ModalView show={modalShow} size={false} fullscreen={true} handleClose={handleClose} headline={modalType[1]} length='0' type={modalType[0]} />
                            
                          </Nav>
                   </Navbar.Collapse>
               </Container>
           </Navbar>) : <></>}
        </Row>        
        </>
    )
}
