import React, {useState, useContext} from 'react'
import { Container, ListGroup, Toast } from 'react-bootstrap'
import AuthContext from '../../context'

export default function ManagePage(props) {

    const {fetchData, userNavbar} = useContext(AuthContext)
    const [show, setShow]         = useState(false)

    function handleClick(e){
        setShow(true)
    } 

    return (
        <Container>
            <p> The menu looks like:</p>
            <Toast onClose={() => setShow(false)} show={show} delay={3000} autohide>
                <Toast.Header>
                    <strong className="me-auto">Oh no!</strong>
                    <small>now</small>
                </Toast.Header>
                <Toast.Body>We have not implemented any functionality here</Toast.Body>
            </Toast>
            <ListGroup>
                 {userNavbar.map( (element) => {
                      return (
                        <ListGroup.Item action variant="secondary" onClick={() => handleClick(element)}>
                           {element.title }
                        </ListGroup.Item>
                     )
                      })}
            </ListGroup>
           

        </Container>
            
       
    )
}
