import React from 'react'

export default function CategoryView(props) {
    const {articleCat, control, handleClick} = props
    
    return (
        <div className="categories d-flex flex-row justify-content-start">
                        {articleCat.map((item, index) => {
                            return(
                                <div className="item" onClick={() => handleClick(item, index)}>{item}{control ? '   âœ–' : ''}</div>
                            )
                        })}
        </div>
    )
}
