import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';

import {BrowserRouter as Router, Switch, Route, Redirect} from 'react-router-dom'
import React, {useState, useEffect} from 'react';

import AuthContext, {ModalContext, ArticleContext, LangContext, PlacesContext} from './context';

import Navigation from './Components/Navbar/Navbar';
import Home from './Components/Pages/Home';
import Widget from './Components/Widget';
import About from './Components/Pages/About';
import Counsel from './Components/Pages/Counseling';
import Login from './Components/Pages/Login'
import BlogPostWrapper from './Components/BlogPostWrapper';
import PageWrapper from './Components/PageWrapper';

import { Container, Row, Col } from 'react-bootstrap';



function App() {
  
  const [collection, setCollection]   = useState([])
  const [authLogin, setAuthLogin]     = useState(false)
  const [modalShow, setModalShow]     = useState(false)
  const [modalType, setModalType]     = useState([])
  const [userNavbar, setUserNavbar]   = useState([])
  const [adminNavbar, setAdminNavbar] = useState([])
  const [lang, setLang]               = useState('en')
  const [uploaded, setUploaded]       = useState({})
  const [places, setPlaces]           = useState([])


 //Functions
  const handleClose = () =>  setModalShow(false);
  const catArray = getArrayOfCategories(collection)


  //GET Data
  async function fetchData(term){
    // const result = await fetch(`https://my-json-server.typicode.com/puckfried/Blog_JsonServer/${term}`)
    const result = await fetch(`https://react-minimal-blogv2.herokuapp.com/${lang}${term}`)
    const res = await result.json();
    settingFetch(res, term)
  }
  //POST, PUT and DELETE
  async function postData(term, method, data) {
    try{
      const res = await fetch(`https://react-minimal-blogv2.herokuapp.com/${lang}${term}`, {
        // const res = await fetch(`https://my-json-server.typicode.com/puckfried/Blog_JsonServer/${term}`, {
        method: method,
        headers: { "Content-type" : "application/json"},
        body: JSON.stringify(data)
        })
      const result = await res.json()
      setUploaded(result)
    }
    catch{
      console.log('something went wrong while sending')
    }}


  function settingFetch(dataFetched, target){
    target === 'ArticleArr' ? setCollection(() => dataFetched) 
                            : target === 'UserNavbar' ? setUserNavbar(() => dataFetched)
                            : target === 'AdminNavbar' ? setAdminNavbar(() => dataFetched)
                            : target === 'Places' ? setPlaces( () => dataFetched)
                            : console.log('nothing to fetch') 
  }


  function getArrayOfCategories(collection){
    const tmpArray =[] 
    collection.map((element) => {
      if (element.category.length > 0) {
        element.category.map((item) => {
          if (!tmpArray.includes(item)){
            tmpArray.push(item)
          }})
        }})
     return tmpArray 
    }


  useEffect(() => {
    fetchData('ArticleArr')
    },[])

  useEffect(() => {
    fetchData('ArticleArr')
  },[uploaded])

  useEffect(() => {
    fetchData('ArticleArr')
    },[lang])

  useEffect(() => {
    fetchData('Places')
  }, [])

  useEffect(() => {
    fetchData('Places')
  }, [lang])



//Render Part

  return (
  <div className="App">
    <Router>
    
    <AuthContext.Provider value={{authLogin, setAuthLogin, fetchData, postData, userNavbar, adminNavbar}}>
    <LangContext.Provider value={{lang, setLang}}>
    <ModalContext.Provider value={{modalShow, setModalShow, modalType,setModalType, handleClose}}>
    <ArticleContext.Provider value={{collection,setCollection, catArray}}>
    <PlacesContext.Provider value={{places, setPlaces}} >
     
      <Navigation />
      <Container className="containerWrapper" fluid>
        <Row lg='2' md='1'>
          <Switch>
            <Col lg={8}>
               {collection.map((element) =>{
                  return(
                      <Route path={`/post/detail/${element.id}`} >  
                        <BlogPostWrapper article={element} detail={element.id} />
                      </Route>
                  )
                })}
                {userNavbar.map((element) => {
                  return(
                    <Route path={`/${element.link}`} >  
                       <PageWrapper title={element.title} src={element.link} />
                    </Route>
                  )
                })}
                <Route path = '/login' >
                  <Login />
                    {authLogin ? <Redirect to='/' /> : <></>}
                </Route>   
                <Route path = '/' exact >
                  <Home article={collection} />
                </Route> 
              </Col>
            </Switch>  
        
            <Col lg={4}>
              <Widget />
            </Col>
          </Row>
      </Container>

    </PlacesContext.Provider>
    </ArticleContext.Provider>
    </ModalContext.Provider>
    </LangContext.Provider>
    </AuthContext.Provider>
    
    </Router>
  </div>
  );
}

export default App;
