import React, {useState, useContext, useEffect} from 'react'
import { ArticleContext, LangContext } from '../context'
import { Container, Row } from 'react-bootstrap'
import BlogPost from './Pages/BlogPost'

function BlogPostWrapper(props) {
    
    const {collection}  = useContext(ArticleContext)
    const {lang}        = useContext(LangContext)
    const [result, setResult] = useState(collection)
    const [currentCat, setCurrentCat] = useState('none')
    const [showDetailId, setShowDetailId] = useState(props.detail)

    function reset(){
        setResult(collection)
        setCurrentCat('none')
    }

    function handleCategoryClick(id){
        if (!showDetailId){
            reset()
            const tmpResult = collection.filter((element) => element.category.includes(id))
            setCurrentCat([id])
            setResult(tmpResult)
            }
    
        else{
            setShowDetailId('')
            }
    }
     
    useEffect( () =>{
        setResult(collection)}, [collection] )

    return (
        <Container>
            {!showDetailId ?
                 <>
                    <h3>{lang==='en' ? "Recent Blog Posts" : "Nachrichten von uns" }</h3>
                    {currentCat!=='none' ? <div onClick={() => reset()}><h6>{`Category: ${currentCat} âœ–`}</h6></div>:<></> } 
                    <Row xl={2} lg={1} sm={1} className='justify-content-md-around'>
                         {result.map((element,index) => {
                             return (
                                <BlogPost
                                    articleDetail={element}
                                    detail={showDetailId}
                                    handleCatClick={handleCategoryClick}
                                />
                     )})}
                     </Row>
                 </> 
            :            
                <BlogPost 
                    articleDetail={result.filter((item) => { return item.id === showDetailId })[0]}
                    handleCatClick={handleCategoryClick}
                    detail={showDetailId}
                />
            }
        </Container>
    )
}

export default BlogPostWrapper
