import React, {useEffect, useState, useContext} from 'react'
import { Modal, Button, Container, Row, Col, Nav, Tab} from 'react-bootstrap'
import ManagePage from './Navbar/ManagePage'
import MapView from './MapView'
import { LangContext } from '../context'

import ManageText from './Navbar/ManageText'
import EditText from './Navbar/EditText'


function ModalView(props) {

  const {fullscreen, size, style, length, show, handleClose, headline, type, mapKey, element} = props
  const {lang, setLang} =useContext(LangContext)
  let [tmpId, setTmpId] = useState(false)

  function handleLangSwitch(newLang){
    setLang(newLang)
  }

  return (
      <>
        <Modal size={size} fullscreen={fullscreen} show={show} onHide={handleClose} contentClassName='modal-90w'>
          <Modal.Header closeButton>
            <Modal.Title>{headline}</Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <Container>
                <Row >
                  <Col className="d-flex justify-content-center">
                  {type === 'addText' ?
                      <ManageText edit={false} article={{headline: '', content: ''}} close={handleClose} /> :
                    type === 'editText' ? 
                      <EditText close={handleClose}/> :
                    type === 'manage' ?
                      <ManagePage /> :
                    type === 'map' ?
                      <MapView zoom={14} mapKey={mapKey} style={style} length={length} zoomControl={true} element={element}/>:
                      <></>
                    }
                   
                  </Col>
                </Row>              
            </Container>
          </Modal.Body>
          <Modal.Footer>
          </Modal.Footer>
        </Modal>
      </>
    );
  }
  
  export default ModalView
 

