import React from 'react'
import { Col } from 'react-bootstrap';
import { Link } from 'react-router-dom'
import CategoryView from '../CategoryView'
import draftToHtml from 'draftjs-to-html';
import {ChevronDoubleDown, ArrowUp} from 'react-bootstrap-icons'


function BlogPost(props) {
   const {articleDetail, handleCatClick, detail} = props
   
   //The Draft.js is transformed to html
   let displayContent = {__html: '<div></div>'}
   if (articleDetail.rawContent){
        const markup = draftToHtml(articleDetail.rawContent);
        displayContent = {__html: markup}
   }
  
   return (
        <>
        <Col  className='blogpost'>
            {detail ? <><Link to='/'><div><ArrowUp />  back to overview</div></Link><br></br></> : <></>}
            <div className='blogpostBorderTop'></div>
            <div className='blogpostInner'> 
                <h4>{articleDetail.headline}</h4>
                <div className='d-flex flex-row justify-content-between'>
                    <p>{articleDetail.date}</p>
                    <p>{articleDetail.author}</p>
                </div>
                <p>{articleDetail.teaser}</p>
                {!detail ? 
                    <Link to={`/post/detail/${articleDetail.id}`}><div className="d-flex flex-column align-items-center readOn"><p>... read more</p><ChevronDoubleDown size={30} className={'readOnArrow'} /></div></Link> 
                    : (<div><div>{articleDetail.content}</div><div dangerouslySetInnerHTML={displayContent}></div></div>)} 
                <br></br>                                      
            </div>
           
            <h6>Categories</h6>
                <CategoryView articleCat={articleDetail.category} handleClick={handleCatClick} /> 
            <br /><br />
            <div className="d-flex flex-row justify-content-center">
                <hr style={{width: '50%'}}></hr>
            </div>
            <div className='blogpostBorderBtm'></div>
        </Col></>
    )
}

export default BlogPost
