import React, {useState, useContext} from 'react'
import { Button, Container, Row, Col, Form, Alert,Tab, Nav } from 'react-bootstrap'
import AuthContext, { ArticleContext } from '../../context'
import CategoryView from '../CategoryView'
import DropdownBtn from '../DropdownBtn'
import FormGroup from '../FormGroup'
import EditorWrapper from '../EditorWrapper'

export default function ManageText(props) {
    const {edit, article, close} = props
    const [headline, setHeadline] = useState(article.headline)
    const [teaser, setTeaser] = useState(article.teaser)
    const [articleBody, setArticleBody] = useState(article.content)
    const [category, setCategory] = useState(article.category || [])
    const [input, setInput] = useState('')
    const [delArt, setDelArt] = useState(false)  
    
    let   [exportDraft, setExportDraft] = useState(article.rawContent)

    const {collection, setCollection, catArray} = useContext(ArticleContext)
    const {postData}                            = useContext(AuthContext)

    const missingCat = category ? catArray.filter((element) => !category.includes(element)) : catArray

    function handleSubmit(e){
        
        e.preventDefault()
        if (edit){
            const tmpArray = collection.map( (element) => {
                if (element.id === article.id) {
                    element.headline = headline
                    element.teaser = teaser
                    element.content = articleBody
                    element.category = category
                    element.rawContent = exportDraft
                    postData(`articleArr/${element.id}`,'PUT', element)
                }
                return element
                })
            // setCollection(tmpArray)
        }
        else{
            postData('articleArr', 'POST', 
            {   id        : new Date,
                author    : "Christoph",
                date      : '24.5.2021',
                category  : category,
                teaser    : teaser,
                headline  : headline,
                content   : articleBody,
                rawContent: exportDraft
            })
            // setCollection([{id : new Date,
            // author    : "Christoph",
            // date      : '24.5.2021',
            // category  : category,
            // teaser    : teaser,
            // headline  : headline,
            // content   : articleBody
            // },...collection])
            }
        close()
        
          
        } 

    function handleDelete(confirm){
       setDelArt(true)
       if (confirm) {
        //    const tmpArray = collection.filter( (element) => element.id !== article.id)
        //    setCollection(tmpArray)
            const tmpArray = collection.map( (element) => {
            if (element.id === article.id) {
                postData(`articleArr/${element.id}`,'DELETE', element)
            }})
            close()
           }
    }

    function removeCategory(item, itemIndex){
        const tmpArray = category.filter( (element,index) => index !== itemIndex )
        setCategory(tmpArray)
        
    }

    function addCategory(item, drop){
        if (drop){
            setCategory([...category, item])
        }
        else {
            setInput(() => item)
            if (item.includes(' ',1) || item.includes(',',1)){
                const tmpString = item.slice(0,-1)
                    if (category.includes(tmpString)) {
                        setInput('')}
                    else {
                        setInput('')
                        setCategory([...category, tmpString])
                    } 
            }
        }
    }
    
    return (
        <Container>
            {edit ?
            <p>Do your changes!</p> :
            <h5> Please Enter a new Article with a related headline</h5>
            }
            <Form onSubmit={(e) => handleSubmit(e)}>
                <FormGroup title="Headline" id="artHead" as="textarea" rows={1} placeholder="Please give me an headline" value={headline} changeFunc={setHeadline} />
                <FormGroup title="Teaser" id="artTeaser" as="textarea" rows={5} placeholder="The teaser is shown on the startpage" value={teaser} changeFunc={setTeaser} />
                <EditorWrapper 
                    exportDraft={exportDraft} 
                    setExportDraft={setExportDraft} 
                />
                {category ? <CategoryView  articleCat={category} handleClick={removeCategory} control={true}/>
                          : <></>
                          }
                    <br></br>
                <Form.Group className="mb-3" controlId="articleCategory" >
                    <Form.Label>Enter new Category or select existing</Form.Label>
                    <DropdownBtn addInput={addCategory} input={input} title={'Select a Category'} dropSelect={missingCat} />
                </Form.Group>

                
                {!delArt ?
                    <div className="d-flex flex-row justify-content-around">
                         <Button variant="success" type="submit">
                                 Save {edit ? 'changes' : "new article"}
                        </Button>
                    {edit ? 
                        <Button variant="danger" onClick={() => handleDelete(false)}>
                                Delete article
                        </Button> : <></>
                    }
                    </div> :
                    <Alert variant='danger' >
                        Are you sure you wanne delete this article?
                        <div className="d-flex flex-row justify-content-end">
                        <Button onClick={() => setDelArt(false)} variant="outline-success" style={{marginRight: "20px"}}>
                                No!
                         </Button>
                       
                        <Button onClick={() => handleDelete(true)} variant="outline-danger">
                                Yes!
                         </Button>
                        </div>
                    </Alert>
                }
            </Form>
        </Container>
            
       
    )
}
